html {
  /* 1 */
  font-family: Montserrat;
  font-size: 15px;
  font-weight: 400;
  line-height: 1.73;
  /* 2 */
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  /* 3 */
  background: #eef6fa;
  color: #4f5a80;
