em {
  color: #7d9cec;
}
/*
 * Insert
 */
ins {
  background: #FBFFC9;
  color: #7B3715;
  text-decoration: none;
}
/*
 * Mark
 */
mark {
  background: #FBFFC9;
  color: #7B3715;
}
/*
 * Quote
