  font-weight: 300;
  color: #314483;
  text-transform: inherit;
  letter-spacing: 0;
}
/* Add margin if adjacent element */
* + h1,
* + .uk-h1,
* + h2,
* + .uk-h2,
* + h3,
* + .uk-h3,
* + h4,
* + .uk-h4,
* + h5,
* + .uk-h5,
* + h6,
* + .uk-h6 {
  margin-top: 40px;
}
/*
 * Sizes
 */
h1,
.uk-h1 {
  font-size: 42px;
  line-height: 1.2;
  font-family: Montserrat;
  font-weight: 300;
}
h2,
.uk-h2 {
  font-size: 32px;
  line-height: 1.3;
  font-family: Montserrat;
  font-weight: 500;
}
h3,
.uk-h3 {
