area,
button,
input,
label,
select,
summary,
textarea {
  touch-action: manipulation;
}
/* Pass media breakpoints to JS
 ========================================================================== */
/*
 * Breakpoints
 */
.var-media-s:before {
  content: '640px';
}
.var-media-m:before {
  content: '960px';
}
.var-media-l:before {
  content: '1200px';
}
.var-media-xl:before {
  content: '1600px';
}
/* ========================================================================
   Component: Link
 ========================================================================== */
/* Muted
 ========================================================================== */
a.uk-link-muted,
.uk-link-muted a {
  color: #8d9ab1;
}
a.uk-link-muted:hover,
.uk-link-muted a:hover {
  color: #4f5a80;
}
/* Text
 ========================================================================== */
/*
 * `!important` needed to override inverse component
 */
a.uk-link-text:not(:hover),
.uk-link-text a:not(:hover) {
