}
.uk-divider-small::after {
  content: "";
  display: inline-block;
  width: 45px;
  max-width: 100%;
  border-top: 2px solid #5872b5;
  vertical-align: top;
  border-image: '';
  border-image-slice: 1;
}
/* ========================================================================
   Component: List
 ========================================================================== */
.uk-list {
  padding: 0;
  list-style: none;
}
/*
 * Micro clearfix
 */
.uk-list > li::before,
.uk-list > li::after {
